                                                <div class="text-center text-sm font-semibold text-gray-900 hidden md:block">{{translate('lesson-price')}}</div>
                                                <div class="text-center text-sm font-semibold text-gray-900 hidden md:block">{{translate('lesson-status')}}</div>
                                                <div class="text-center text-sm font-semibold text-gray-900">{{translate('lesson-tool')}}</div>
                                            </div>
                                        </div>
    
                                        <div class="divide-y divide-gray-300 bg-white relative" :data-content="translate('database-connect')" :class="[(activeBlock?'isblock' : 'isunblock')]">
    
                                            <div v-if="listItems.length === 0" class="p-3 text-center text-gray-500">
                                                <div>
                                                    <font-awesome-icon :icon="['fas','exclamation-circle']" class="" /> </div>
                                            </div>
    
                                            <div v-for="(course, index) in visibleItems" :key="course._id" :class="index % 2 === 0 ? 'bg-indigo-50' : 'bg-white'">
                                                <div class="grid grid-cols-6 gap-1 p-2">
                                                    <div class="col-span-6 md:col-span-3">
                                                        <router-link :to="'/lesson/detail/'+course._id" class="text-gray-700 hover:text-indigo-600">
                                                            <div class="flex items-center">
                                                                <div class="mr-2 font-bold">{{(currentPage - 1) * limitItem + index + 1}}.</div>
                                                                <div class="relative w-[50px] h-[50px] mr-2 rounded-sm bg-gray-200">
                                                                    <img
                                                                    v-if="course.cover"
                                                                    :src="course.cover"
                                                                    class="w-full h-full rounded-sm object-cover"
                                                                    alt="Lesson Image"
                                                                    />
                                                                    <div v-else class="absolute inset-0 flex justify-center items-center">
                                                                    <span class="text-gray-400 font-bold text-xs">IMG</span>
                                                                    </div>
                                                                </div>
                                                                <div class="flex-1">
                                                                    <CourseIcon :title="course.name" :chcek="course.ref" :order="(currentPage - 1) * limitItem + index + 1" />
                                                                    <span :class="{'ml-2 px-1 text-green-500 bg-green-100 font-semibold text-xs': course.lesson_type === 'master', 'ml-2 px-1 text-gray-700 bg-gray-100 font-semibold text-xs': course.lesson_type === 'child' }">
                                                                    {{ course.lesson_type === 'child' ? 'CH' : 'MS' }}
                                                                    </span>
                                                                    <p class="font-normal text-gray-500 text-sm">{{ course.slug }}</p>
                                                                </div>
                                                            </div>
                                                        </router-link>
                                                    </div>
                                                    <div class="hidden md:block text-center text-gray-500">
                                                        <Price :regular="course.regular_price" :sale="course.sale_price" />
                                                    </div>
                                                    <div class="hidden md:block">
                                                        <StatusLabel :check="course.status" />
                                                        <span class="text-xs font-medium p-1 ml-2 text-white" :class="{'bg-orange-600': course.sync, 'bg-gray-500': !course.sync, 'text-white': course.sync}">{{ course.sync ? course.sync : 'N/A' }}</span>
                                                        <span class="text-xs font-medium p-1 ml-2 text-white bg-blue-600"><i class="fa-solid fa-users mr-1"></i> {{course.enrollUserCount}}  </span>
                                                    </div>
                                                    <div class="col-span-6 md:col-span-1">
                                                        <div class="flex items-center justify-center sm:justify-end">
    
                                                            <div class="text-center justify-center">
                                                                <button @click="cloneData(course.master)" v-tooltip="''" type="button" class="mr-1 p-2 inline-flex justify-center rounded-sm border border-gray-600 bg-gray-500 text-sm font-medium text-white shadow-sm hover:bg-gray-900 focus:outline-none focus:ring-2 focus:ring-gray-900 focus:ring-offset-2">
                                                                <font-awesome-icon :icon="['fas','copy']" class="w-3 h-3 text-white" />
                                                                <span class="leading-none ml-1">{{ course.child !== undefined ? course.child.length : 0 }}</span>
                                                                </button>
                                                                                <button @click="$router.push('/lesson/edit/' + course._id)" v-tooltip="''" type="button" class="mr-1 p-2 inline-flex justify-center rounded-sm border border-gray-300 bg-white text-sm font-medium text-gray-700 shadow-sm hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-900 focus:ring-offset-2">
                                                                <font-awesome-icon :icon="['fas','square-pen']" class="w-3 h-3 text-gray-600" />
                                                                <!-- <span></span> -->
                                                                </button>
                                                                                <button @click="removeItem(course._id, course.master, course.name)" v-tooltip="''" type="button" class="mr-1 p-2 inline-flex justify-center rounded-sm border border-gray-300 bg-white text-sm font-medium text-gray-700 shadow-sm hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-900 focus:ring-offset-2">
                                                                <font-awesome-icon :icon="['fas','trash-can']" class="w-3 h-3 text-gray-600" />
                                                                <!-- <span></span> -->
                                                                </button>
                                                                                <!-- <button v-if="course.cover && course.cover.includes('https://content.fti.academy/')" @click="checkUrl(course.cover, course._id)" type="button" class="mr-1 inline-flex justify-center rounded-sm border border-gray-300 bg-white px-1 py-1 text-sm font-medium text-gray-700 shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-gray-900 focus:ring-offset-2">
                                                                <font-awesome-icon :icon="['fas','trash-can']" class="w-3 h-3 text-gray-600 mr-1 mt-1" />
                                                                <span>Sync</span>
                                                                </button> -->
                                                                                <button @click="$router.push('/lesson/detail/' + course._id + '/enroll')" v-tooltip="''" type="button" class="mr-1 p-2 inline-flex justify-center rounded-sm border border-gray-300 bg-white text-sm font-medium text-gray-700 shadow-sm hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-900 focus:ring-offset-2">
                                                                <font-awesome-icon :icon="['fas','users']" class="w-3 h-3 text-gray-600" />
                                                                <!-- <span></span> -->
                                                                </button>
                                                            </div>
    
                                                        </div>
                                                    </div>
                                                </div>
    
                                                <div class="grid grid-cols-3 pl-1 pr-1 pb-1 border-t border-gray-200">
                                                    <div class="col-span-2">
                                                        <p class="font-medium mt-1">
                                                            <template v-if="Array.isArray(course.categoryData) && course.categoryData.length > 0">
                                                <a v-for="(categoryItem, index) in course.categoryData" :key="categoryItem._id" :href="index === 0 && !isMobile ? '#' : undefined" class="text-xs font-medium bg-indigo-600 pl-2 pr-2 pt-1 pb-1 mr-1 rounded text-white mb-1" :class="{ 'hidden sm:inline-block': index > 0 || isMobile }">
                                                  {{ categoryItem.name }}
                                                </a>
                            </template>

                            <template v-else>
                                <span class="text-xs font-medium bg-gray-600 pl-2 pr-2 pt-1 pb-1 mr-1 rounded text-white"></span>
                            </template>
                            </p>
                            
                          </div>
                          <div class="col-span-1 flex justify-end">
                            <span class="mt-1 text-base font-medium pl-2 pr-2 pt-1 pb-1 mr-1 rounded text-gray-300">{{ course.type }}</span>
                          </div>
                        </div>

                      </div>

                      <!-- Lazy Loading Indicators -->
                      <template v-if="lazy">
                        <!-- Loading More Indicator -->
                        <div v-if="isLoadingMore" class="flex justify-center items-center py-6 border-b border-gray-200">
                          <div class="flex items-center gap-3 text-blue-600">
                            <div class="animate-spin rounded-full h-5 w-5 border-b-2 border-blue-600"></div>
                            <span class="text-sm font-medium">...</span>
                          </div>
                        </div>
                        
                        <!-- All Items Loaded -->
                        <div v-else-if="!hasMoreItems && listItems.length > initialDisplayCount" class="flex justify-center items-center py-6 border-b border-gray-200">
                          <div class="flex items-center gap-2 text-green-600">
                            <i class="fas fa-check-circle"></i>
                            <span class="text-sm font-medium"> {{ listItems.length }} </span>
                          </div>
                        </div>
                        
                        <!-- Auto-scroll hint for remaining items -->
                        <div v-else-if="hasMoreItems" class="flex justify-center items-center py-4 border-b border-gray-200">
                          <div class="flex flex-col items-center gap-2">
                            <div class="flex items-center gap-2 text-gray-500 text-sm">
                              <i class="fas fa-mouse"></i>
                              <span> ( {{ listItems.length - currentDisplayCount }} )</span>
                            </div>
                            <!-- Emergency show all button -->
                            <button
                              @click="currentDisplayCount = listItems.length"
                              class="px-3 py-1 bg-blue-600 text-white text-xs rounded hover:bg-blue-700 transition-colors"
                              title=" auto-scroll "
                            >
                              <i class="fas fa-eye mr-1"></i>
                              
                            </button>
                          </div>
                        </div>
                      </template>
                    </div>
                    
                    <!-- Pagination Footer -->
                    <div class="p-0 m-0 bg-gray-50">
                      <div colspan="6" class="p-3 m-0">
                        <!-- Lazy Mode Info -->
                        <div v-if="lazy" class="mb-2 text-sm text-gray-600 text-center">
                           {{ visibleItems.length }}  {{ listItems.length }}  
                          ( {{ currentPage }},  {{ totalItems }} )
                        </div>
                        
                        <div class="text-center">
                          <Pagination :current-page="currentPage" :total-items="totalItems" :total-pages="totalPages" :limit-items="limitItem" :data-loading="loading" :display-mode="'summary'" @page-changed="handlePageChanged" />
                        </div>
                      </div>
                    </div>
                    
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
</div>
</template>

<style>
input:focus-visible {
    outline: none;
}

.opacity-bg-indigo-50 {
    background-color: rgba(125, 150, 178, 0.5);
    /* Adjust the RGB color values and opacity as needed */
}
</style>